import pageStatus from '../../components/status/status.vue';
import shopsHeader from './shopsHeader.vue';
import infoModel from './infoModel.vue';
import activity from './activity.vue';
import goods from './goods.vue';
import goodsLists from './goodsLists.vue';
import shopCart from './shopCart.vue';
import seller from './seller.vue';
import comments from './comments.vue';
import foodView from './food.vue';
import interfaces from '../../utils/interfaces.js';
import shopCartClass from '../../common/shopCartClass.js';

export default {
	data() {
		return {
			shopInfo: null,
			showInfoModel: false,
			tabNav: [{ name: '点餐' }, { name: '评价' }, { name: '商家' }],
			currentIndex: 0,
			scrollTop: 0,
			statusBarHeight: 0,
			showStatus: false,
			recommend: {},
			goodList: {},
			cartSlectList: [],
			selectedFood: null,
			showFood: false
		};
	},
	onLoad(option) {
		// console.log(option)
		if (!option.shop_id) {
			uni.switchTab({
				url: '../tabBar/home/home'
			});
			return;
		}
		this.initData();
	},
	watch: {









	},
	mounted() {






	},
	components: {
		pageStatus,
		shopsHeader,
		infoModel,
		activity,
		goods,
		goodsLists,
		shopCart,
		seller,
		comments,
		foodView
	},
	onPageScroll(e) {



	},
	onPullDownRefresh() {
		setTimeout(() => {
			this.initData();
			uni.stopPullDownRefresh();
		}, 1000);
	},
	methods: {
		upCountData() {
			let that = this;
			uni.getStorage({
				key: 'goodsList',
				success: res => {
					res.data.forEach(function(item) {
						that.getCartStorage(item);
					});
					this.cartSlectList = res.data;
				},
				fail() {}
			});
		},
		getCartStorage(list) {
			this.goodList.menu.map(item => {
				return item.foods.map(v => {
					if (v.food_id === list.food_id) {
						return (v.food_count = list.food_count);
					}
				});
			});

			this.recommend.recommend[0].items.map(v => {
				if (v.food_id === list.food_id) {
					return (v.food_count = list.food_count);
				}
			});
		},
		handleFood(food) {
			// console.log(food)
			this.selectedFood = food;
			this.showFood = true;
		},
		initData() {
			this.request({
				url: interfaces.getShops,
				success: res => {
					this.shopInfo = res.rst;
					this.recommend = this._normalizeRecommend(res.recommend);
					this.goodList = this._normalizeMenu(res.menu);
					this.upCountData();
				}
			});
		},
		_normalizeRecommend(list) {
			let that = this;
			let map = {
				recommend: []
			};
			list.forEach(res => {
				let recomItem = [];
				res.items.forEach(item => {
					recomItem.push(that.newClass(item));
				});
				map.recommend.push({
					name: res.name,
					items: recomItem
				});
			});
			return map;
		},
		_normalizeMenu(list) {
			let that = this;
			let map = {
				menu: []
			};

			list.forEach(res => {
				let foodsItem = [];
				res.foods.forEach(item => {
					foodsItem.push(that.newClass(item));
				});
				map.menu.push({
					icon_url: res.icon_url,
					name: res.name,
					description: res.description,
					foods: foodsItem
				});
			});
			return map;
		},
		newClass(item) {
			return new shopCartClass({
				food_id: item.specfoods[0].food_id,
				food_name: item.specfoods[0].name,
				food_image: item.image_path,
				food_count: 0,
				food_price: item.specfoods[0].price,
				month_sales: item.month_sales,
				satisfy_rate: item.satisfy_rate,
				description: item.description
			});
		},
		clickNav(index) {
			this.currentIndex = index;
		},
		add(id) {
			this.addAllList(id);
		},
		sub(id) {
			this.subAllList(id);
		},
		addAllList(id) {
			this.goodList.menu.map(item => {
				return item.foods.map(v => {
					if (v.food_id === id) {
						return v.food_count++;
					}
				});
			});

			this.recommend.recommend[0].items.map(v => {
				if (v.food_id === id) {
					return v.food_count++;
				}
			});

			this.setAllList();
		},
		subAllList(id) {
			this.goodList.menu.map(item => {
				return item.foods.map(v => {
					if (v.food_id === id) {
						return v.food_count--;
					}
				});
			});

			this.recommend.recommend[0].items.map(v => {
				if (v.food_id === id) {
					return v.food_count--;
				}
			});
			this.setAllList();
		},
		setAllList() {
			let arr = [];
			this.goodList.menu.forEach(item => {
				item.foods.forEach(list => {
					if (list.food_count > 0) {
						arr.push(list);
					}
				});
			});
			this.recommend.recommend[0].items.forEach(item => {
				if (item.food_count > 0) {
					arr.push(item);
				}
			});
			this.cartSlectList = this.uniq(arr);
			this.setCartStorage(this.cartSlectList);
		},
		uniq(arr) {
			//数组对象去重
			let obj = {};
			return arr.reduce((cur, next) => {
				obj[next.food_id] ? '' : (obj[next.food_id] = true && cur.push(next));
				return cur;
			}, []); //设置cur默认类型为数组，并且初始值为空的数组
		},
		setCartStorage(goodsList) {
			// 存储到本地存储中
			uni.setStorage({
				key: 'goodsList',
				data: goodsList,
				success: function(res) {}
			});
		},
		empty() {
			let that = this;
			uni.removeStorage({
				key: 'goodsList',
				success: function(res) {
					that.cartSlectList = [];
					that.goodList.menu.map(item => {
						return item.foods.map(v => {
							return v.food_count = 0;
						});
					});

					that.recommend.recommend[0].items.map(v => {
						return v.food_count = 0;
					});
				}
			});
		},
		scroll() {}
	}
};
